import * as React from "react";
import * as Loadable from "react-loadable";
import { Switch, Route } from "react-router";
import NotFound from "./NotFound";

const Loading = (props: Loadable.LoadingComponentProps) => <div>{props.pastDelay}</div>;

const Main = Loadable({
    loader: () => import("./Main"),
    loading: Loading,
    delay: 10000
});

// 유니온플레이스 탭
const Union = Loadable({
    loader: () => import("./Union/Index"),
    loading: Loading,
    delay: 10000
});

const Vision = Loadable({
    loader: () => import("./Union/Vision"),
    loading: Loading,
    delay: 10000
});

const Culture = Loadable({
    loader: () => import("./Union/Culture"),
    loading: Loading,
    delay: 10000
});

const Person = Loadable({
    loader: () => import("./Union/Person"),
    loading: Loading,
    delay: 10000
});

const Sharing = Loadable({
    loader: () => import("./Union/Sharing"),
    loading: Loading,
    delay: 10000
});

// 프로젝트 탭
const Project = Loadable({
    loader: () => import("./Project/Index"),
    loading: Loading,
    delay: 10000
});
const Seocho = Loadable({
    loader: () => import("./Project/Seocho"),
    loading: Loading,
    delay: 10000
});
const Dangsan = Loadable({
    loader: () => import("./Project/Dangsan"),
    loading: Loading,
    delay: 10000
});

// 브랜드 탭
const Brand = Loadable({
    loader: () => import("./Brand/Index"),
    loading: Loading,
    delay: 10000
});

const Upflo = Loadable({
    loader: () => import("./Brand/Upflo"),
    loading: Loading,
    delay: 10000
});

const Joyland = Loadable({
    loader: () => import("./Brand/Joyland"),
    loading: Loading,
    delay: 10000
});

const Nextkitchen = Loadable({
    loader: () => import("./Brand/Nextkitchen"),
    loading: Loading,
    delay: 10000
});

const Upfit = Loadable({
    loader: () => import("./Brand/Upfit"),
    loading: Loading,
    delay: 10000
});

const Sullivan = Loadable({
    loader: () => import("./Brand/Sullivan"),
    loading: Loading,
    delay: 10000
});

// 홍보탭
const Promotion = Loadable({
    loader: () => import("./Promotion/Index"),
    loading: Loading,
    delay: 10000
});

const Video = Loadable({
    loader: () => import("./Promotion/Video"),
    loading: Loading,
    delay: 10000
});

// 채용탭
const UnionPeople = Loadable({
    loader: () => import("./UnionPeople"),
    loading: Loading,
    delay: 10000
});

const Career = Loadable({
    loader: () => import("./Career"),
    loading: Loading,
    delay: 10000
});

export default () => (
    <Switch>
        <Route exact={true} path="/" component={Main} />
        {/* 유니온 탭 */}
        <Route exact={true} path="/union" component={Union} />
        <Route exact={true} path="/union/vision" component={Vision} />
        <Route exact={true} path="/union/culture" component={Culture} />
        <Route exact={true} path="/union/person" component={Person} />
        <Route exact={true} path="/union/sharing" component={Sharing} />
        {/* 프로젝트 탭 */}
        <Route exact={true} path="/project" component={Project} />
        <Route exact={true} path="/project/seocho" component={Seocho} />
        <Route exact={true} path="/project/dangsan" component={Dangsan} />

        {/* 브랜드 탭 */}
        <Route exact={true} path="/brand/planning" component={Brand} />
        <Route exact={true} path="/brand/upflo" component={Upflo} />
        <Route exact={true} path="/brand/joyland" component={Joyland} />
        <Route exact={true} path="/brand/nextkitchen" component={Nextkitchen} />
        <Route exact={true} path="/brand/upfit" component={Upfit} />
        <Route exact={true} path="/brand/sullivan" component={Sullivan} />

        {/* 홍보 탭 */}
        <Route exact={true} path="/promotion" component={Promotion} />
        <Route exact={true} path="/promotion/video" component={Video} />

        {/* 채용 탭 */}
        <Route exact={true} path="/union-people" component={UnionPeople} />
        <Route exact={true} path="/career" component={Career} />

        <Route component={NotFound} />
    </Switch>
);



