import * as React from "react";
import styled from "styled-components";

import logoImg from "../../assets/logo.png";
// import menuImg from "../../assets/menu.svg";
// import careerImg from "../../assets/career.png";
import { Link } from "react-router-dom";
import { Global } from "../../containers/App";

interface MenuProps {
    status: string;
    nowPage?: string;
}

const Wrapper = styled.header`
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 9998;
    min-width: 1100px;
    background-color: rgba(255, 255, 255, 0.97);
    -webkit-transition: top 0.4s ease-in-out;
    transition: top 0.4s ease-in-out;

    width: 100%;
    height: 80px;

    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #231916;
    padding: 0 40px;

    &.down {
        top: -80px;
    }
`;

const Logo = styled(Link)`
    background: url(${logoImg}) no-repeat top left;
    background-size: 100% 100%;
    outline: none;
    border: none;
    height: 38px;
    width: 135px;
    margin-left: 20px;
`;

const MenuBox = styled.div`
    display: flex;

    height: 100%;
    margin-right: 20px;
`;

const MenuItem = styled(Link)`
    height: 100%;

    width: 150px;
    display: flex;
    align-items: center;
    text-decoration: none;
    /* padding: 0 0 0 18px; */
    color: black;
    box-sizing: border-box;
    /* margin: 0 20px 0 0; */
    justify-content: center;
    & > span {
        position: relative;
    }

    & > span:before {
        content: "";
        position: absolute;
        left: -1px;
        bottom: -7px;
        width: 100%;
        height: 2px;
        background-color: #231916;
        transform-origin: 0 0;
        transform: scaleX(0);
        transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
    }

    &.now > span:before {
        transform: scaleX(1);
    }

    &:hover {
        /* border-bottom: 2px solid #000; */
        & > span:before {
            transform: scaleX(1);
        }

        & > div {
            display: flex;
            transform: scaleY(1);

            &::before {
                content: "";
            }
            &::after {
                content: "";
                border-left: 1px solid #fff;
            }
        }
    }
`;

const MenuDetailBox = styled.div`
    display: flex;
    position: absolute;
    justify-content: center;
    left: 0;
    right: 0;
    top: 80px;
    height: 280px;
    z-index: 10000;
    transform-origin: 0 0;
    transform: scaleY(0);
    transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
    background-color: #fff;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;

    & > div {
        width: 150px;
        border-left: 1px solid #eee;
        padding: 28px 28px 0 28px;
        font-size: 14px;

        &:nth-last-child(1) {
            font-size: 15px;
            border-right: 1px solid #eee;
        }

        &:hover {
            background-color: #fff;
        }
    }
`;

const MenuDetail = styled.div`
    display: flex;
    text-decoration: none;
    color: black;
    flex-direction: column;
    font-size: 15px;
    box-sizing: border-box;

    & > a.emphasis:first-child {
        font-weight: bold;
        color: black;
        position: relative;
        margin-bottom: 7px;

        &::before {
            content: "";
            position: absolute;
            left: -1px;
            bottom: 0px;
            width: calc(100% + 2px);
            height: 1px;
            background-color: #231916;
            transform-origin: 0 0;
            transform: scaleX(0);
            transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
        }

        &:hover::before {
            content: "";
            transform: scaleX(1);
        }
    }
    & > a {
        text-decoration: none;
        color: #4c4c4c;
        margin-bottom: 3px;
        position: relative;

        &:hover {
            color: black;
        }
    }
`;

const ToolBox = styled.div`
    height: 100%;
    display: flex;

    width: 135px;
`;

const LangBox = styled.div`
    display: flex;
    width: 135px;

    align-items: center;
    font-size: 12px;
    margin: 0 20px 0 0;
    & > span:nth-child(2) {
        width: 1px;
        height: 10%;
        background-color: #231916;
        margin: 0 20px 0 20px;
    }
`;

const DropDown = () => (
    <MenuDetailBox>
        <MenuDetail>
            <Link to={"/union/"}>기업</Link>
            <Link to={"/union/vision"}>비전</Link>
            <Link to={"/union/culture"}>문화</Link>
            <Link to={"/union/person"}>사람</Link>
            <Link to={"/union/sharing"}>나눔</Link>
        </MenuDetail>
        <MenuDetail>
            <Link to={"/project/"}>부동산개발</Link>
            <Link to={"/project/seocho"}>서초</Link>
            <Link to={"/project/dangsan"}>당산</Link>
        </MenuDetail>
        <MenuDetail>
            <Link to={"/brand/planning"}>브랜드기획</Link>
            <Link to={"/brand/upflo"}>업플로</Link>
            <Link to={"/brand/sullivan"}>카페 설리번</Link>
            <Link to={"/brand/nextkitchen"}>넥스트 키친</Link>
            <Link to={"/brand/joyland"}>조이랜드</Link>
            <Link to={"/brand/upfit"}>업핏</Link>
        </MenuDetail>
        <MenuDetail>
            <Link to={"/promotion/"}>보도자료</Link>
            <Link to={"/promotion/video"}>브랜드 영상</Link>
        </MenuDetail>
        <MenuDetail>
            <Link to={"/union-people"}>유니언피플</Link>
            <Link to={"/career"}>채용</Link>
            {/* <Link to={"/promotion/video"}>유니언피플</Link> */}
            {/* <Link to={"/promotion/"}>채용 절차</Link> */}
            {/* <Link to={"/promotion/video"}>지원하기</Link> */}
        </MenuDetail>
    </MenuDetailBox>
);

const Menu: React.SFC<MenuProps> = props => {
    return (
        <>
            <Global />
            <Wrapper className={props.status}>
                <Logo to={"/"} />
                <MenuBox>
                    <MenuItem to={"/union"} className={props.nowPage === "union" ? "now" : ""}>
                        <span>유니언플레이스</span>
                        <DropDown />
                    </MenuItem>
                    <MenuItem to={"/project"} className={props.nowPage === "project" ? "now" : ""}>
                        <span>프로젝트</span>
                        <DropDown />
                    </MenuItem>
                    <MenuItem to={"/brand/planning"} className={props.nowPage === "brand" ? "now" : ""}>
                        <span>브랜드</span>
                        <DropDown />
                    </MenuItem>
                    <MenuItem to={"/promotion"} className={props.nowPage === "promotion" ? "now" : ""}>
                        <span>홍보</span>
                        <DropDown />
                    </MenuItem>
                    <MenuItem to={"/career"} className={props.nowPage === "career" || props.nowPage === "union-people" ? "now" : ""}>
                        <span>인재채용</span>
                        <DropDown />
                    </MenuItem>
                </MenuBox>
                <ToolBox>
                    <LangBox>{/* <span>KOR</span>
                        <span />
                        <span>ENG</span> */}</LangBox>
                </ToolBox>
            </Wrapper>
        </>
    );
};

export default Menu;



