import * as React from "react";
import styled from "styled-components";
import logo from "../../assets/logo_white.png";

// import clear from "../../assets/clear.png";
import add from "../../assets/add.png";
import { Global } from "../../containers/App";

interface FooterProps {}

const Wrapper = styled.footer`
    width: 100%;
    background-color: #2f2e2e;
    height: 80px;
    display: flex;
    align-items: center;
    font-size: 13px;
    color: white;
    position: relative;

    & > img {
        height: 50%;
        margin-left: 30px;
    }

    & > span {
        color: white;
        margin-top: 13px;
    }

    & > span:nth-child(3) {
        margin-left: 25%;
    }
`;

const DropDown = styled.div`
    border-bottom: 1px solid #fff;
    position: absolute;
    display: flex;
    width: 140px;
    right: 20px;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 2px;
    cursor: pointer;

    & > img {
        width: 14px;
        height: 14px;
    }
`;

const ItemContainer = styled.div`
    display: ${(props: { show: boolean }) => (props.show ? "flex" : "none")};
    flex-direction: column;
    position: absolute;
    background: white;
    border: 1px solid #eee;
    bottom: calc(100% + 4px);
    width: 100%;
    padding: 14px;
    & > a {
        margin-bottom: 5px;
        color: black;
        text-decoration: none;
        padding-bottom: 1px;
        position: relative;

        &:hover:before {
            content: "";
            background-color: #000;
            width: 100%;
            height: 1px;
            position: absolute;
            bottom: 0;
        }
    }
`;

const Footer: React.SFC<FooterProps> = props => {
    const [show, setShow] = React.useState(false);
    return (
        <>
            <Global />
            <Wrapper>
                <img src={logo} alt="" />
                <span>© 2018 by UNION PLACE.</span>
                <span>
                    서울특별시 영등포구 당산로 241
                    <br />
                    TEL. 02-2677-1214 │ FAX. 02-2677-1215
                </span>
                <DropDown onClick={() => setShow(!show)}>
                    <span>계열 브랜드</span>
                    <img src={add} alt="" />
                    <ItemContainer show={show}>
                        <a href={"http://www.upflohostel.com"}>업플로 호스텔</a>
                        <a href={"http://www.upflohouse.com"}>업플로 하우스</a>
                        <a href={"http://www.sullivan.co.kr"}>카페설리번</a>
                        <a href={"http://www.nextkitchen.co.kr"}>넥스트키친</a>
                        <a href={"http://www.joylandenglish.com"}>조이랜드</a>
                        <a href={"http://www.upfit.co.kr"}>업핏</a>
                    </ItemContainer>
                </DropDown>
            </Wrapper>
        </>
    );
};

export default Footer;



