import * as React from "react";
import Menu from "../components/Menu/Menu";
import { createGlobalStyle } from "styled-components";
import Footer from "../components/Footer/Footer";
import Routes from "./route";
import { withRouter, RouteComponentProps } from "react-router";
import font from "../assets/fonts/GT-Walsheim-Bold.ttf";

interface AppProps extends RouteComponentProps {}
interface AppState {
    scroll: string;
    nowPage: string;
}
export const Global = createGlobalStyle`
    @font-face {
        font-family: 'GT Walsheim';
        src: url(${font});
    }

    @font-face {
        font-family: 'Noto Sans KR';
        src: url(${require("../assets/fonts/NotoSansKR-Bold.otf")});
        font-weight: bold;
        font-style: normal;
    }

    @font-face {
        font-family: 'Noto Sans KR';
        src: url(${require("../assets/fonts/NotoSansKR-Regular.otf")});
        font-weight: normal;
        font-style: normal;
    }

    * {
        box-sizing: border-box;
        padding: 0;
        margin: 0;
    }

    *:lang(en) { 
        font-family:'GT Walsheim';
    } 
    *:lang(ko) { 
        font-family:'Noto Sans KR', sans-serif;
    } 

    body, html {
        height: 100%;
        margin: 0;
        padding: 0;
    }
    #root {
        height: 100%;
    }

    a {
        color: black;
        text-decoration: none;
    }
`;

class App extends React.Component<AppProps, AppState> {
    public prevScrollY = 0;
    constructor(props: AppProps) {
        super(props);
        this.state = {
            scroll: "",
            nowPage: ""
        };
    }

    public componentDidMount() {
        window.addEventListener("scroll", this.handleScroll, { passive: true });
    }

    public componentWillReceiveProps(nextProps) {
        const nowPage = this.checkMenu(nextProps.location.pathname);
        console.log(nowPage);
        this.setState({ nowPage });
    }

    public checkMenu(s: string) {
        if (s.includes("union-people")) {
            return "union-people";
        } else if (s.includes("project")) {
            return "project";
        } else if (s.includes("brand")) {
            return "brand";
        } else if (s.includes("promotion")) {
            return "promotion";
        } else if (s.includes("career")) {
            return "career";
        } else if (s.includes("union")) {
            return "union";
        } else {
            return "";
        }
    }

    public componentWillUnmount() {
        window.removeEventListener("scroll", this.handleScroll);
    }

    public handleScroll = () => {
        if (window.scrollY > this.prevScrollY) {
            this.setState({ scroll: "down" });
        } else {
            this.setState({ scroll: "up" });
        }
        this.prevScrollY = window.scrollY;
    };

    public render() {
        return (
            <>
                <Global />
                <div lang="ko">
                    <Menu status={this.state.scroll} nowPage={this.state.nowPage} />
                    <main style={{ paddingTop: 80 }}>
                        <Routes />
                    </main>
                    <Footer />
                </div>
            </>
        );
    }
}

export default withRouter(App);



